:root {
  --mainBlue: rgb(20, 13, 218);
  --mainWhite: #fff;
  --grey: rgb(214, 214, 214);
}


body {
  background-color: var(--mainWhite);
}

img {
  width: 100%;
  height: auto;
}

h1 {
  font-family: "Archivo Black", serif;
  color: var(--mainWhite);
  font-size: 300%;
  text-align: left;
  max-width: 500px;
}
h2 {
  font-family: "Archivo Black", serif;
}

p {
  margin: 0 0 15px 0;
}



button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: var(--mainBlue);
  color: var(--mainWhite);
  cursor: pointer;
  box-shadow: 0 0 5px 0 grey;
}

button:active {
  box-shadow: none;
}

.hero-container{
  position: relative;
  background-image: linear-gradient(25deg, rgb(98, 93, 245), rgb(20, 13, 218), rgb(52, 45, 242));
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  z-index: 0;
}

/* HEADER */
.App-header {
  display: flex;
  padding-top: 2%;
  padding-left: 5%;
  align-items: center;
}

.logo {
  width: 50px;
  height: auto;
}

.logo img {
  width: 100%;
  height: auto;
}

.App-header p {
  color: var(--mainWhite);
  font-weight: bold;
  margin-left: 15px;
}

/* HERO */

.hero {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  height: 80vh;
  padding: 2% 5% 0 5%;
  overflow: hidden;
}

.hero-text {
  display: flex;
  flex-direction: column;
}

.hero-text p {
  color: var(--mainWhite);
  font-size: 1em;
  text-align: left;
  max-width: 500px;
}

.hero-img {
  position: absolute;
  max-width: 460px;
  width: 50%;
  height: auto;
  right: 5%;
  bottom: -3px;
  z-index: -1;
  transform: translateX(10%);
}

/* TEE SECTION */
.tee-section {
  margin: 50px 0;
}

.tee-section-text {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 25px;
}

.tee-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  position: relative;
  margin: 1% auto 2% auto;
  max-width: 85%;
}

.tee-customizer-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  padding: 20px;
}

.customizer-header {
  display: flex;
  margin-top: 0;
  padding: 5px 0px;
  color: var(--mainBlue);
  font-size: 2em;
  text-align: left;
  justify-content: center;
}


.palette-label {
  margin-bottom: 8px;
}



/* color palette */
.color-palette {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  justify-content: flex-start;
  margin-bottom: 50px;
  max-width: 500px;
  width: 80%;
}

.color-div {
  transition: border 0.5s ease;
}

  /* TEXT VISIBLE ON MOBILE */
  .hidden-text {
    display: none;
  }


/* TEE SELECTION */
.tee-selection {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  background-color: var(--grey);
  padding: 5% 0;
  border-radius: 10px;
}

.tee-shirt-img {
  position: relative;
  width: 75%;
  max-width: 500px;
  height: auto;
  margin: 0 auto;
}
.tee-shirt-img img {
  width: 100%;
  height: auto;
}

.label-overlay {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  max-width: 500px;
  width: 100%;
  height: auto;
}

/* FORM */
.form-container {
  display: flex;
  width: 100%;
}

.form-box {
  transition: all 2s ease-in;
}

.section-left {
  position: absolute;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 1% 2%;
  background-color: var(--mainWhite);
  box-shadow: 0 0 5px 0 grey;
  max-width: 550px;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.close-form {
  position: absolute;
  top: 3%;
  right: 5%;
  cursor: pointer;
  padding: 5px 10px;
  color: var(--mainBlue);
  font-size: 1.5em;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80%;
  margin-top: 8%;
}

form h3 {
  color: var(--mainBlue);
}

label {
  align-self: flex-start;
  margin-bottom: 5px;
}

input {
  box-sizing: border-box;
  margin-bottom: 20px;
  padding: 0 10px;
  width: 100%;
  height: 35px;
  outline: none;
  border: 1px solid var(--mainBlue);
  border-radius: 5px;
  background-color: transparent;
}

.tiny {
  font-size: 13px;
}


/* THANK YOU PAGE */
.thank-you-page {
  box-sizing: border-box;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-image: linear-gradient(25deg, rgb(10, 49, 71), rgb(21, 99, 145), rgb(31, 147, 214));
  color: var(--mainWhite);
  padding: 0px 25px;
}
.big {
  font-size: xx-large;
  font-weight: bold;
}


/* FOOTER */
.footer {
  width: 100%;
  margin-top: 10px;
  padding: 15px 0;
  text-align: center;
  background-color: var(--mainBlue);
  color: var(--mainWhite);
}

/* RESPONSIVE CSS */

@media screen and (max-width: 768px) {
  h1 {
    font-size: 200%;
    text-align: center;
  }

  .hero-container {
    background-image: linear-gradient(25deg, rgb(98 93 245 / 90%), rgb(20 13 218 / 90%), rgb(52, 45, 242));
    z-index: unset;
    overflow: hidden;
  }

  .hero {
    justify-content: center;
  }
  
  .hero-img {
    width: 200%;
    height: auto;
    left: 50%;
    bottom: -3px;
    transform: translateX(-50%);
    z-index: -1;
  }

  .customizer-header {
    font-size: 1.5em;
  }

  .tee-container {
    display: flex;
    flex-direction: column;
  }

  /* TEXT VISIBLE ON MOBILE */
  .hidden-text {
    display: block;
  }
  .color-palette {
    margin-bottom: 20px;
  }

  .section-left {
    padding: 10% 5%;
    height: 55%;
    width: 110%;
  }

  .fa-solid, .fas {
    font-size: 0.7em;
}

  .form {
    margin-top: 5%;
  }

  .tiny {
    font-size: 10px;
    margin-bottom: 0;
    margin-top: 10px;
  }

  .tee-selection {
    height: 100%;
  }
}
